printReviewStar = (review) => {
	const row = [];
	for (var i = 0; i < review; i++) {
	  row.push(<i class="fas fa-star filled"></i>);
	}
	  for (var i = 0; i < (5-review); i++) {
	  row.push(<i class="fas fa-star "></i>);
	}
	return row;
  };

